export const rong_getConversationList = (instance,callback) => {	
	/*
	http://www.rongcloud.cn/docs/web_api_demo.html#
		http://www.rongcloud.cn/docs/web.html#5_2
		http://www.rongcloud.cn/docs/api/js/Conversation.html

	https://developer.rongcloud.cn/service/roam/rXxI4IAJjxRAD72SpQ==

	
		1  
		2
	*/


	var conversationTypes = [RongIMLib.ConversationType.PRIVATE,RongIMLib.ConversationType.GROUP];  //
	var limit = 150; //null
	var start = new Date().getTime();
	// var instance = RongIMClient.getInstance();

	instance.getConversationList({
		onSuccess: function(list) {
			// list.sort(function(a,b){
			// 	return a.sentTime > b.sentTime;
			// });
			
			callback(list);
		},
		onError: function(error) {
			console.log("");
            // showResult("",error,start);
		}
    }, conversationTypes, limit);
}

